.contact-us-contain {
    background: #F3F8FB;
    .contact-us-content {
        padding: 120px 198px 120px 198px;
    }
    .contact-us-title {
        font-weight: 700;
        font-size: 48px;
        line-height: 120%;
        text-align: center;
        text-transform: capitalize;
        color: #222222;
        padding-bottom: 60px;
        text-transform: uppercase;
    }
    .contact-us-contact-background {
        height: 444px;
    }
    .contact-us-contact-phone {
        padding-left: 0px;
        background: #FFFFFF;
        box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.1);
        position: relative;
        right: 100px;
        top: 56px;
    }
    .contact-phone-left {
        padding: 40px 0px 48px 32px;
    }
    .contruy-name {
        font-weight: 400;
        font-size: 18px;
        line-height: 120%;
        color: #222222;
        flex: none;
        order: 0;
        flex-grow: 0;
    }
    .number-phone {
        font-weight: 600;
        font-size: 20px;
        line-height: 130%;
        color: #222222;
        padding-top: 12px;
    }
    .contact-phone-right {
        padding-top: 40px;
    }
    .icon-phone {
        position: relative;
    }
    .icon-phone-img {
        position: absolute;
        bottom: -138px;
    }
    .title-email {
        font-size: 18px;
        line-height: 120%;
        color: #FFFFFF;
    }
    .content-email {
        font-size: 20px;
        line-height: 130%;
        color: #FFFFFF;
        font-weight: 600;
    }
}