.technology-contain {
    padding-top: 160px;
    // padding-left: 100px;
    .technology-title-contain {
        text-align: center;
        color: #222222;
         > div {
            font-weight: 700;
            font-size: 48px;
            line-height: 120%;
            padding-bottom: 16px;
        }
        > span {
            font-weight: 400;
            font-size: 18px;
            line-height: 150%;
            padding-top: 16px;
        }
    }
    .technology-conttent-contain {
        padding-top: 60px;
    }
    .technology-contain-box {
        padding-right: 70px;
    }
    .technology-box {
        padding-left: 0px;
        box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.1);
    }
    .technology-box-icon {
        width: 170px;
        background: #1EB2FF;
        height: 88px;
        padding-left: 0px;
        text-align: center;
        padding-top: 16px;
    }
    .technology-box-text {
        background: #ffffff;
        padding-left: 33px;
        padding-top: 30px;
        font-weight: 500;
        font-size: 20px;
        line-height: 140%;
        color: #222222;
        width: 100%;
    }
    .technology-slider {
        padding-top: 100px;
        filter: drop-shadow(0px 0px 30px rgba(0, 0, 0, 0.16));
        position: relative;
        padding-left: 0px;
        padding-right: 0px;
    }
    .technology-slider-background {
        position: absolute;
        top: 30px;
        left: 100px;
    }
    .next-slider {
        text-align: end;
        position: relative;
        bottom: 220px;
        right: -25px;
        cursor: pointer;
    }
    .prev-slider {
        position: relative;
        bottom: 265px;
        right: 20px;
        cursor: pointer;
    }
    .technology-slider-background-img {
        width: 800px;
        height: 400px;
    }
    .icon-slider {
        padding-top: 20px;
    }
    .icon-slider-nav {
        padding-left: 50%;
    }
}