.TechnologySolutions-contain {
    padding-top: 160px;
    // padding-left: 100px;
    padding-right: 25px;
    .technology-title-contain {
        text-align: center;
        color: #222222;
         > div {
            font-weight: 700;
            font-size: 48px;
            line-height: 120%;
        }
        > span {
            font-weight: 400;
            font-size: 18px;
            line-height: 150%;
            padding-top: 16px;
        }
    }
    .technology-conttent-contain {
        padding-top: 60px;
    }
    .technology-contain-box {
        padding-right: 70px;
    }
    .technology-box {
        padding-left: 0px;
        box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.1);
    }
    .technology-box-icon {
        width: 170px;
        background: #1EB2FF;
        height: 124px;
        padding-left: 0px;
        text-align: center;
        padding-top: 35px;
    }
    .technology-box-text {
        background: #ffffff;
        padding-left: 42px;
        padding-top: 48px;
        font-weight: 500;
        font-size: 20px;
        line-height: 140%;
        color: #222222;
        width: 100%;
    }
    .technology-contain-box-right {
        padding-left: 0px;
        padding-right: 0px;
    }
}