.contain-banner {
    text-align: center;
    padding-top: 48px;
    .contain-banner-backgound {
        height: 544px;
    }
    .banner-logo-tma {
        padding-top: 48px;
    }
    .banner-title {
        font-weight: 700;
        font-size: 56px;
        line-height: 140%;
        text-align: center;
        color: #FFFFFF;
        padding-top: 24px;
    }
    .banner-title-nav {
        font-weight: 400;
        font-size: 28px;
        line-height: 140%;
        text-align: center;
        color: rgba(255, 255, 255, 0.9);
        padding-top: 12px;
    }
    .banner-title-slider-2 {
        font-weight: 700;
        font-size: 56px;
        line-height: 140%;
        text-align: center;
        color: #FFFFFF;
        padding-top: 87px;
    }
    .banner-title-nav-2 {
        font-weight: 700;
        font-size: 56px;
        line-height: 140%;
        text-align: center;
        color: #FFFFFF;
    }
    .banner-title-slider-3 {
        font-weight: 700;
        font-size: 56px;
        line-height: 140%;
        text-align: center;
        color: #FFFFFF;
        padding-top: 42px;
    }
    // .banner-contain-logo-three {
    //     padding-left: 100px;
    // }
    .banner-logo-three {
        background: #FFFFFF;
        box-shadow: 0px 0px 30px rgb(0 0 0 / 10%);
        height: 270px;
        position: relative;
        top: 55px;
        left: 10px;
    }
    .banner-logo-three-slider-2 {
        background: #FFFFFF;
        box-shadow: 0px 0px 30px rgb(0 0 0 / 10%);
        height: 270px;
        position: relative;
        top: 85px;
        left: 10px;
    }
    .banner-logo-three-slider-3 {
        background: #FFFFFF;
        box-shadow: 0px 0px 30px rgb(0 0 0 / 10%);
        height: 372px;
        position: relative;
        top: 20px;
        left: 10px;
    }
    .map-slider-3 {
        padding-top: 10px;
    }
    .logo-tma-three {
        padding-top: 80px;
    }
    .logo-tma-three-nav {
        width: 770px;
    }
    .banner-bg-right {
        text-align: end;
    }
    .icon-banner-slider {
        padding-top: 60px;
    }
    .icon-banner-slider-2 {
        padding-top: 90px;
    }
    .icon-banner-slider-3 {
        padding-top: 20px;
    }
    .mySlider {
        display: block;
    }
    .slick-slide img {
        display: initial; 
    }
    .banner-slider2-engineer {
        justify-content: center;
        font-size: 28px;
        padding-top: 12px;
        color: #222222;
        > b {
            padding-right: 5px;
        }
    }
    .banner-slider2-backgoud-bg {
        margin-left: -111px;
    }
    @media (max-width: 1024px) {
        .logo-tma-three-nav {
            width: 550px;
        }
    }

}
