.title-menu {
    list-style-type: none;
    font-size: 15px;
    font-weight: 500;
    line-height: 140%;
    padding-top: 12px;
    justify-content: end;
    text-transform: uppercase;
    padding-right: 0px;
}
.menu-tag-a {
    text-decoration: none;
    color:#222222
}
.menu-tag-a:hover {
    text-decoration: none;
    color:#222222
}
.menu-scroll {
    position: fixed;
    z-index: 9999;
    width: 100%;
    height: 70px;
    background-color: #ffffff;
}

@media (max-width: 1024px) {
    .menu-scroll {
        height: 100px;
    }
}